import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store'
// 创建 axios 实例，将来对创建出来的实例，进行自定义配置
// 避免污染原始的 axios 实例
const instance = axios.create({
  // 使用相对路径，开发环境会走代理，生产环境会使用完整路径
  baseURL: '/api',
  timeout: 5000,
  headers: { platform: 'h5' }
})
// 自定义请求/响应拦截器
// Add a request interceptor
instance.interceptors.request.use(function (config) {
  // Do something before request is sent
  // 开启loading，禁止背景点击 （截流处理避免多次发送请求）
  Toast.loading({
    message: '加载中...',
    forbidClick: true, // 禁止背景点击
    duration: 0 // 不会自动消失
  })
  // 只要有token，就在请求时携带，便于请求需要授权的接口
  const token = store.getters.token
  if (token) {
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }

  // 处理生产环境的API地址
  if (process.env.NODE_ENV === 'production') {
    config.url = `http://smart-shop.itheima.net/index.php?s=/api${config.url}`
  }

  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么 (默认axios会多包装一层data，需要响应拦截器中处理一下)
  const res = response.data
  // 清除loading效果
  Toast.clear()

  // 如果返回的数据中包含data字段，直接返回data
  if (res.data !== undefined) {
    return res.data
  }
  // 如果没有data字段但有message字段，说明可能是错误响应
  if (res.message) {
    Toast(res.message)
    return Promise.reject(res.message)
  }
  // 其他情况直接返回响应数据
  return res
}, function (error) {
  // 清除loading效果
  Toast.clear()
  // 显示错误信息
  Toast.fail(error.message || '请求失败')
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error)
})

// 导出配置好的实例
export default instance
